import { fromJS } from 'immutable'
import { actionTypes } from '../actions'

const initialState = fromJS({
  state: 'bootstrap'
})

export default function (state = initialState, action) {
  switch (action.type) {
    case actionTypes.INIT:
      return state.merge({
        state: 'init'
      })
    default:
      return state
  }
}



