import React from 'react'
import icon from '../../../assets/icon.jpg'

export default class Menu extends React.Component {
  state = {
    openMenu: false
  }

  menuItemClick = () => {
    this.setState({
      openMenu: !this.state.openMenu
    })
  }

  render () {
    return (
      <nav className='navbar navbar-expand-lg navbar-light bg-light'>
        <a className='navbar-brand' href='#'>
          <img src={icon} width='30' height='30' alt='' />
          <span className='brand-name'> Github Stats </span>
        </a>

        <button onClick={this.menuItemClick} className='navbar-toggler'>
          <span className='navbar-toggler-icon' />
        </button>

        <div className={`collapse navbar-collapse ${this.state.openMenu && 'show'}`} id='navbarSupportedContent'>
          <ul className='navbar-nav mr-auto'>
            <li className='nav-item active'>
              <a className='nav-link' href='#'> Home <span className='sr-only'>Home</span></a>
            </li>
            <li className='nav-item'>
              <a className='nav-link' target='_blank' href='http://sizzy.co'> Test with sizzy </a>
            </li>
          </ul>
        </div>
      </nav>
    )
  }
}



