import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import { TableList } from 'react-web-components/dist/Components/Table'
import { getRepositories } from '../redux/selectors'

export const mapStateToProps = createSelector(
  [getRepositories],
  (items = []) => {
    return {
      title: 'Repositories',
      headers: ['ID', 'Repository', 'Description', 'Stars'],
      rows: ['id', 'name', 'description', 'stars'],
      theme: 'red',
      items: items.toJS()
    }
  }
)

export default connect(mapStateToProps)(TableList)



