import { put, call, takeLatest } from 'redux-saga/effects'
import { actions, actionTypes } from '../actions'
import { repository, user } from 'github-public-api'
import { getLastMonthDate, getLastYearDate } from '../../helpers/date'

const baseQuery = (query = {}, params = {}) => ({
  query: {
    ...query
  },
  params: {
    per_page: 5,
    ...params
  }
})

const repositoriesQuery = baseQuery(
  {
    archived: false,
    created: `>${getLastMonthDate()}`
  },
  {
    sort: 'stars',
    direction: 'desc'
  }
)

const userQuery = baseQuery(
  {
    created: `>${getLastYearDate()}`
  },
  {
    sort: 'followers',
    direction: 'desc'
  }
)

export function * getRepositories () {
  const response = yield call(repository, repositoriesQuery)

  if (response.isOk()) {
    yield put(actions.fetchSearchRepositoriesSuccess({
      headers: response.headers,
      status: response.status,
      count: response.data.total_count,
      items: response.data.items
    }))
  } else {
    yield put(actions.fetchSearchRepositoriesError({
      headers: response.headers,
      status: response.status,
      data: response.data
    }))
  }
}

export function * getUsers () {
  const response = yield call(user, userQuery)

  if (response.isOk()) {
    yield put(actions.fetchSearchUsersSuccess({
      headers: response.headers,
      status: response.status,
      count: response.data.total_count,
      items: response.data.items
    }))
  } else {
    yield put(actions.fetchSearchUsersError({
      headers: response.headers,
      status: response.status,
      data: response.data
    }))
  }
}

export default function * init () {
  yield takeLatest(actionTypes.FETCH_SEARCH_REPOSITORIES, getRepositories)
  yield takeLatest(actionTypes.FETCH_SEARCH_USERS, getUsers)
}



