import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import { getRateLimit } from '../redux/selectors'
import { TableList } from 'react-web-components/dist/Components/Table'
import { epochToDate } from '../helpers/date'
export const mapStateToProps = createSelector(
  getRateLimit,
  (rateLimit = []) => {
    return {
      title: 'Github Rate Limit',
      headers: ['x-RateLimit-Limit', 'x-RateLimit-Remaining', 'x-RateLimit-Reset'],
      rows: ['xRateLimitLimit', 'xRateLimitRemaining', 'xRateLimitReset'],
      theme: 'blue',
      items: [{
        xRateLimitLimit: rateLimit.get('xRateLimitLimit'),
        xRateLimitRemaining: rateLimit.get('xRateLimitRemaining'),
        xRateLimitReset: epochToDate(rateLimit.get('xRateLimitReset'))
      }]
    }
  }
)

export default connect(mapStateToProps)(TableList)



