// import moment or support format or date `yyyy-mm-dd`

export const dateToYMD = (date) => {
  var d = date.getDate()
  var m = date.getMonth() + 1
  var y = date.getFullYear()
  return '' + y + '-' + (m <= 9 ? '0' + m : m) + '-' + (d <= 9 ? '0' + d : d)
}

export const dateToHMS = (date) => {
  var h = date.getHours()
  var m = date.getMinutes()
  var s = date.getSeconds()
  return (h <= 9 ? '0' + h : h) + ':' + (m <= 9 ? '0' + m : m) + ':' + (s <= 9 ? '0' + s : s)
}

export const epochToDate = (utcSeconds) => {
  if (!utcSeconds) return ''

  var d = new Date(0)
  d.setUTCSeconds(utcSeconds)
  return dateToYMD(d) + ' ' + dateToHMS(d)
}

export const getLastMonthDate = () => {
  var now = new Date()
  now.setMonth(now.getMonth() - 1)
  return dateToYMD(now)
}

export const getLastYearDate = () => {
  var now = new Date()
  now.setYear(now.getFullYear() - 1)
  return dateToYMD(now)
}



