import { fromJS } from 'immutable'
import { actionTypes } from '../actions'

const initialState = fromJS({
  rateLimit: {},
  users: {
    items: []
  },
  repositories: {
    items: []
  }
})

export default function (state = initialState, action) {
  switch (action.type) {
    case actionTypes.FETCH_SEARCH_REPOSITORIES_SUCCESS:
      return state.merge({
        rateLimit: action.payload.headers,
        repositories: {
          items: action.payload.items
        }
      })
    case actionTypes.FETCH_SEARCH_USERS_SUCCESS:
      return state.merge({
        rateLimit: action.payload.headers,
        users: {
          items: action.payload.items
        }
      })
    case actionTypes.FETCH_SEARCH_REPOSITORIES_ERROR:
    case actionTypes.FETCH_SEARCH_USERS_ERROR:
      return state.merge({
        rateLimit: action.payload.headers
      })
    default:
      return state
  }
}



