import { generateActions } from 'redux-action-generator'
import { repositoriesSerializer } from './serializers/repositories'
import { usersSerializer } from './serializers/users'

const actionsList = [
  // Repositories actions
  { type: 'EXECUTE_SEARCH_REPOSITORIES' },
  { type: 'FETCH_SEARCH_REPOSITORIES' },
  { type: 'FETCH_SEARCH_REPOSITORIES_SUCCESS', serializer: repositoriesSerializer },
  { type: 'FETCH_SEARCH_REPOSITORIES_ERROR' },

  // Users Actions
  { type: 'EXECUTE_SEARCH_USERS' },
  { type: 'FETCH_SEARCH_USERS' },
  { type: 'FETCH_SEARCH_USERS_SUCCESS', serializer: usersSerializer },
  { type: 'FETCH_SEARCH_USERS_ERROR' }
]

export default generateActions(actionsList)



