import { connect } from 'react-redux'
import GitHubActions from '../Components/GitHubActions'
import github from '../redux/actions/github'

const mapDispatchToProps = (dispatch) => ({
  executeSearchRepositories: () => dispatch(github.actions.executeSearchRepositories()),
  executeSearchUsers: () => dispatch(github.actions.executeSearchUsers())
})

export default connect(null, mapDispatchToProps)(GitHubActions)



