import { List, Map } from 'immutable'

export const getGithub = (state) => state.get('github', Map())

export const getRateLimit = (state) => getGithub(state).get('rateLimit', Map())
export const getUsers = (state) => getGithub(state).get('users').get('items', List())
export const getRepositories = (state) => getGithub(state).get('repositories').get('items', List())



