import { put, take, call, race, fork } from 'redux-saga/effects'
import { actions, actionTypes } from '../actions'
import { delay } from 'redux-saga'

const GITHUB_FETCH_TIMER_INTERVAL = 2 * 60 * 1000

export function * getUsers () {
  while (true) {
    yield race({
      execute: take(actionTypes.EXECUTE_SEARCH_USERS),
      timeout: call(delay, GITHUB_FETCH_TIMER_INTERVAL)
    })

    yield put(actions.fetchSearchUsers())
  }
}

export function * getRepositories () {
  while (true) {
    yield race({
      execute: take(actionTypes.EXECUTE_SEARCH_REPOSITORIES),
      timeout: call(delay, GITHUB_FETCH_TIMER_INTERVAL)
    })

    yield put(actions.fetchSearchRepositories())
  }
}

export default function * init () {
  yield fork(getRepositories)
  yield fork(getUsers)
}



