const serializeItems = (items = []) => {
  return items.map((item) => ({
    id: item.id,
    avatar: item.avatar_url,
    login: item.login
  }))
}

export const usersSerializer = (data) => {
  const { headers, status, count, items } = data

  return {
    headers,
    status,
    count,
    items: serializeItems(items)
  }
}



