import React from 'react'
import Button from 'react-web-components/dist/Components/Button'

export default ({ executeSearchRepositories, executeSearchUsers }) => (
  <div>
    <Button id='hot_repo' key='hot_repo' onClick={executeSearchRepositories} > Hot Repositories </Button>
    <Button id='prolific_users' key='prolific_users' onClick={executeSearchUsers} > Prolific Users </Button>
  </div>
)



